import axios from "axios";
import { D as DIR } from "../../../chunks/config.js";
const load = async ({ url }) => {
  const searchParams = url.searchParams.get("q");
  const data = await axios.get(`${DIR}/api/search/${searchParams}`).then((res) => res.data);
  return {
    images: data.images
  };
};
export {
  load
};
